.App {
  text-align: center;
  min-height: 100vh;
  background-color: #0a0a0a;
  color: white;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
}

.counter {
  position: absolute;
  top: 20px;
  right: 20px;
  font-size: 1.2em;
  color: #ff4444;
  text-shadow: 0 0 10px rgba(255, 68, 68, 0.5);
}

.sound-toggle {
  position: absolute;
  top: 20px;
  left: 20px;
  background: none;
  border: none;
  color: white;
  cursor: pointer;
  padding: 10px;
  width: 40px;
  height: 40px;
}

.sound-toggle svg {
  width: 100%;
  height: 100%;
}

.response-container {
  margin-top: 100px;
  max-width: 800px;
  width: 100%;
}

.meme-response {
  opacity: 0;
  animation: fadeIn 0.5s ease-out forwards;
}

.meme-container {
  width: 100%;
  height: 280px;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  background: rgba(0, 0, 0, 0.2);
  border-radius: 10px;
  margin: 15px 0;
}

.meme-image {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
  border-radius: 10px;
}

.try-again-button {
  margin-top: 20px;
  padding: 10px 20px;
  font-size: 1.2em;
  background-color: #ff4444;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: transform 0.2s, background-color 0.2s;
}

.try-again-button:hover {
  background-color: #ff6666;
  transform: scale(1.05);
}

.try-again-button:active {
  transform: scale(0.95);
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@media (max-width: 768px) {
  .meme-container {
    height: 230px;
  }
  
  .counter {
    font-size: 1em;
  }
}